import React, { useContext, useEffect, useState } from "react";
// scss 
import "../../css/cart_list.scss";
import { dCon } from "../func/dCon";
// 
import $, { data } from "jquery";
import { Link } from "react-router-dom";

function CartList({optVal}) {


  // API 
  const myCon = useContext(dCon);

  // console.log(myCon.setCartList);
  console.log("Detailpg->myCon.optVal.current->Layout.jsx  <CartList optVal={optVal}/>  ",optVal.current);
  
  //DetailPg.jsx size    [[]]
  const cartData= optVal.current;
  //cartData[,,,];

  //  
  const localsData=JSON.parse(localStorage.getItem("mycart-data"));
  

  // -> ////////////////////////////////////////////  
  const [showCart,setShowCart]= useState(true);//true  
  useEffect(()=>{
      if(showCart){
        $(".addedcart-box").animate({ right: "0" });
        document.querySelector("html").style.overflow = "hidden";
        $(".cartlist-bg").fadeIn(300);
      }
      else{
        $(".addedcart-box").animate({ right: "-100%" });
        document.querySelector("html").style.overflow = "auto";
        $(".cartlist-bg").fadeOut(300);
        setTimeout(() => {
          myCon.setCartList(false);
        }, 300); 
      }
      return () => {//~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~      
        $(".addedcart-box").stop(true, true);//.addedcart-box  
        $(".cartlist-bg").stop(true, true);//.cartlist-bg  
        document.querySelector("html").style.overflow = "auto"; //html  
        clearTimeout();// 
      };
  },[showCart])



  return (
    <>
      <div className="cartlist-box">
        <div className="cartlist-bg"></div>
        <div className="addedcart-box">
          {/*   X  */}
          <button
            className="cbtn"
            onClick={() => {
              setShowCart(false);
              
              //????????? ( )
              // myCon.setCartList(false);// false ,   {cartData[3]}   ....
            }}
          >
         <img src={process.env.PUBLIC_URL +"/images/icons/x.svg"} alt="x" />
          </button>
          <div className="itembox-wrap">
            <span className="addmsg">{cartData[3]}   </span>
            <div className="item-box">
              <div className="item-img"><img src={process.env.PUBLIC_URL+cartData[1]} alt="img" /></div>
              <div className="item-txt">
                <p>{cartData[0]}</p>
              <p>{cartData[2]}</p>
              </div>
            </div>
              <Link to={"/mycartselpg"}>
            <button
            className="look-my-cart"
            onClick={()=>
              setShowCart(false)
              }>
               
              </button>
              </Link>
          </div>
          <div className="recommend-txt"></div>
          <div className="yu-gi-e-recommendcomp">
               /  
            /   / 
             /   /
              /  
            /   / 
             /   /
              /  
            /   / 
             /   /
              /  
            /   / 
             /   /
              /  
            /   / 
             /   /
              /  
            /   /
          </div>
        </div>
      </div>
    </>
  );
}

export default CartList;
