//    - Member.jsx
import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";

//   JS
import { initData } from "../func/mem_fn";

//  CSS 
import "../../css/join_member.scss";

function Member() {
  //   
  const goNav = useNavigate();

  // goNav( , state)

  // [    ]
  // 1.    
  // 2.    
  //     .
  // 3.     .
  // ->  :
  //   
  // + submit  

  // [  ] /////////////
  // [1]  
  // 1. 
  const [userId, setUserId] = useState("");
  // 2. 
  const [pwd, setPwd] = useState("");
  // 3. 
  const [chkPwd, setChkPwd] = useState("");
  // 4. 
  const [userName, setUserName] = useState("");
  // 5.  
  const [phone, setPhone] = useState("");

  // [2]  
  // ->   (false)
  // 1. 
  const [userIdError, setUserIdError] = useState(false);
  // 2. 
  const [pwdError, setPwdError] = useState(false);
  // 3. 
  const [chkPwdError, setChkPwdError] = useState(false);
  // 4. 
  const [userNameError, setUserNameError] = useState(false);
  // 5.  
  const [phoneError, setPhoneError] = useState(false);

  console.log(">>>>", userIdError);

  // [    ] ////
  const msgId = [
    "   .",

    "    .",

    " ",
  ];

  // [    ]
  const msgEtc = {
    pwd: "8   /, ,   .",

    confPwd: "  ",

    req: " .",

    phone: "   ",
  }; ///// msgEtc ///////

  // [3]   :  msgId[0]
  // ->   
  const [idMsg, setIdMsg] = useState(msgId[0]);

  // [    ] ///////
  // 1.    ////////////
  const changeUserId = (e) => {
    //  
    let val = e.target.value;

    // 1.   (  !)
    const valid =
      /^(([^<>()\[\].,;:\s@"]+(\.[^<>()\[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/i;
    //  : .test()

    // 2.   : e.target
    // console.log(val);

    // 3.  
    // 3-1.   (  )
    if (valid.test(val)) {
      console.log("...!");
      //      !
      initData();
      //  "mem-data"  !

      //     !!!
      // 1.  
      let memData = localStorage.getItem("mem-data");
      console.log(memData);

      // 2.   (? !)
      memData = JSON.parse(memData);
      console.log(memData);
      // ->  !
      // : JSON    
      //    (  )

      // 3.    
      //    !
      //  true,  false
      let isT = memData.some((v) => v.uid === val);
      console.log("id?", isT);

      // 4. true     
      if (isT) {
        //   
        setIdMsg(msgId[1]);
        //  
        setUserIdError(true);
      } ///// if /////
      // 5. false   [ ] 
      else {
        //   :  !(false)
        setUserIdError(false);
      } ///// else //////

      // [   : some() ]
      // ->      true
      // ) every()   false false
      // let isT = memData.some(v=>{
      //     console.log("!",v.uid);
      //     return v.uid===val;
      // });
      // let isT = memData.every(v=>{
      //     console.log("!",v.uid);
      //     return v.uid===val;
      // });

      //   (false)
      //   setUserIdError(false);
    } /// if /////////////////////////
    // 3-2.  :   
    else {
      console.log("~!");
      //   
      setIdMsg(msgId[0]);
      //   (true)
      setUserIdError(true);
    } /// else ///

    //  userId   
    //  !
    setUserId(val);
  }; ////////// changeUserId  ////////////

  // 2.    ///////////
  const changePwd = (e) => {
    //  
    let val = e.target.value;

    // 1.   (  !)
    // const valid = /^.*(?=^.{5,15}$)(?=.*\d)(?=.*[a-zA-Z])(?=.*[!@#$%^&+=]).*$/;  //, ,   5~15
    const valid = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/;

    // 2.   : e.target ->   
    // console.log(val);

    // 3.    
    if (valid.test(val)) setPwdError(false);
    else setPwdError(true);

    // 4.  
    setPwd(val);
  }; ///////// changePwd  //////////

  // 3.    ///////////
  const changeChkPwd = (e) => {
    //  
    let val = e.target.value;

    // 1.    
    if (pwd === val) setChkPwdError(false);
    else setChkPwdError(true);

    // 2.  
    setChkPwd(val);
  }; ///////// changeChkPwd  //////////

  // 4.    ///////////
  const changeUserName = (e) => {
    //  
    let val = e.target.value;

    // 1. 
    if (val !== "") setUserNameError(false);
    else setUserNameError(true);

    // 2.  
    setUserName(val);
  }; ///////// changeUserName  //////////

  // 5.   ///////////
  const changephone = (e) => {
    //  
    let val = e.target.value;

    // 1.   (  !)
    const valid = /^(010|011|012|013|014|015|016|017|018|019)\d{3,4}\d{4}$/;

    // 2.   : e.target ->   
    // console.log(val);

    // 3.    
    if (valid.test(val)) setPhoneError(false);
    else setPhoneError(true);

    // 4.  
    setPhone(val);
  }; ///////// changephone  //////////

  // [    ] ///////////////////////////////////////////////////////////////////////////////////////////
  const totalValid = () => {
    // 1.     !
    if (!userId) setUserIdError(true);
    if (!pwd) setPwdError(true);
    if (!chkPwd) setChkPwdError(true);
    if (!userName) setUserNameError(true);
    if (!phone) setPhoneError(true);

    // 2.  true,  false 
    //  :  +   false
    if (
      userId &&
      pwd &&
      chkPwd &&
      userName &&
      phone &&
      !userIdError &&
      !pwdError &&
      !chkPwdError &&
      !userNameError &&
      !phoneError
    )
      return true;
    //  false false !
    else return false;
  }; /////////// totalValid  ///////////

  // [   ] ////////////////
  const onSubmit = (e) => {
    // 1.  
    e.preventDefault();

    console.log(":", totalValid());

    // 2.   
    if (totalValid()) {
      console.log("! !");

      // [  ]

      // 1.  ( !)
      initData();

      // 2.  
      let memData = localStorage.getItem("mem-data");

      // 3.  
      memData = JSON.parse(memData);

      //     (idx)
      let temp = memData.map((v) => v.idx);
      //    +1!
      console.log(":", Math.max(...temp) + 1);

      // 4.   
      let newData = {
        idx: Math.max(...temp) + 1,
        uid: userId,
        pwd: pwd,
        unm: userName,
        phone: phone,
      };

      // 5.   :    push()
      memData.push(newData);

      // 6.   :  !
      localStorage.setItem("mem-data", JSON.stringify(memData));

      // 7.   +   
      //   
      document.querySelector(".sbtn").innerHTML = "~";
      // 1  :  navigate 
      setTimeout(() => {
        goNav("/login"); // :   (/)   Member   
      }, 1000);
    } ///////// if /////////
    // 3.  /////
    else {
      alert("Change your input!");
    } //// else ///////////
  }; /////////// onSubmit  //////////

  //  
  //   const arr = [{"idx":"100"}, {"idx":"77"}, {"idx":"3"}, {"idx":"44"}, {"idx":"5"}];
  //   const newArr = arr.map(v=>v.idx);
  //   // ... ->    !
  //   const maxValue = Math.max(...newArr);
  //   const minValue = Math.min(...newArr);
  // //   const maxValue = Math.max("77","55","33");
  //   console.log(newArr);
  //   console.log(":",maxValue);
  //   console.log(":",minValue);

  //   //////////////////
  return (
    <div className="outbx">
      <section className="join-member">
        <span className="logtit"> </span>
        <form action="process.php" method="post">
          <ul>
            <li>
              {/* 1.  **************************************************************************************************************** */}
              <input
                className="loginput"
                type="text"
                maxLength="30"
                placeholder=" "//placeholder       .
                value={userId}
                onChange={changeUserId}
              />
              <label></label>
              {
                //      
                //  && 
                userIdError && (
                  <div className="msg">
                    <small>{idMsg}</small>
                  </div>
                )
              }
              {
                //   
                //  && 
                //  : userId  
                // userId  false !
                !userIdError && userId && (
                  <div className="msg">
                    <small>{msgId[2]}</small>
                  </div>
                )
              }
            </li>
            {/* **************************************************************************************************************** */}
            <li>
              <input
                className="loginput"
                type="password"
                maxLength="30"
                placeholder=" "
                value={pwd}
                onChange={changePwd}
              />
              <label></label>
              {
                //    
                //  && 
                pwdError && (
                  <div className="msg">
                    <small>{msgEtc.pwd}</small>
                  </div>
                )
              }
            </li>
            {/*  **************************************************************************************************************** */}
            <li>
              <input
                className="loginput"
                type="password"
                maxLength="30"
                placeholder=" "
                value={chkPwd}
                onChange={changeChkPwd}
              />
              <label> </label>
              {
                //    
                //  && 
                chkPwdError && (
                  <div className="msg">
                    <small>{msgEtc.confPwd}</small>
                  </div>
                )
              }
            </li>
            {/* **************************************************************************************************************** */}
            <li>
              <input
                className="loginput"
                type="text"
                maxLength="20"
                placeholder=" "
                value={userName}
                onChange={changeUserName}
              />
              <label></label>
              {
                //    
                //  && 
                userNameError && (
                  <div className="msg">
                    <small>{msgEtc.req}</small>
                  </div>
                )
              }
            </li>
            {/*  **************************************************************************************************************** */}
            <li>
              <input
                className="loginput"
                type="text"
                maxLength="11"
                placeholder=" "
                value={phone}
                onChange={changephone}
              />
              <label></label>
              {
                //    
                //  && 
                phoneError && (
                  <div className="msg">
                    <small>{msgEtc.phone}</small>
                  </div>
                )
              }
            </li>
            <li style={{ overflow: "hidden" }}>
              <button className="sbtn" onClick={onSubmit}>
                 
              </button>
            </li>
            <li>
               ?
              <Link to="/login">Log In</Link>
            </li>
          </ul>
        </form>
      </section>
    </div>
  );
}

export default Member;
