import React, { useEffect } from "react";

//  woman
import { wNew, wSale } from "../data/products_woman";
//  man
import { mBestSeller, mNew, mSale, mOrigins } from "../data/products_man";

//https://www.npmjs.com/package/react-icons  
import { IoMdHeartEmpty } from "react-icons/io";
import { IoMdHeart } from "react-icons/io";

import "../../css/ProductList.scss";
import { Link } from "react-router-dom";
import { addComma } from "../func/common_fn";
import { useState } from "react";

function ProductList({ dbName }) {
    


    const pdbutton = ["", "", "", "( )"];

    const selData = {
        wNew: wNew,
        wSale: wSale,
        mBestSeller: mBestSeller,
        mNew: mNew,
        mSale: mSale,
        mOrigins: mOrigins,
    };

    //seldata     -    
    const reversedData = selData[dbName].slice().reverse();

    /* *************************************************************  
  //      1  
  const chgOpFn = () => {
    const winH = window.innerHeight * 0.99; 
    const chgop = document.querySelectorAll(".chgop");

    chgop.forEach((v, i) => {
      const pos = v.getBoundingClientRect().top;
      //     .1  on  
      if (pos < winH && !v.classList.contains("on")) {
        setTimeout(() => {
          v.classList.add("on");
        }, 100 * i);
      }
    });
  };

  useEffect(() => {
    //    
    window.addEventListener("scroll", chgOpFn);
    window.addEventListener("touchstart", chgOpFn);//
    window.addEventListener("touchmove", chgOpFn);//

    //       
    return () => {
      window.removeEventListener("scroll", chgOpFn);
      window.removeEventListener("touchstart", chgOpFn);// 
      window.removeEventListener("touchmove", chgOpFn);// 
    };
  }, []); //       

 ****************************************************************** */
    return (
        <>
       
            <div className="pdbutton-box">
                {pdbutton.map((v, i) => (
                    <button key={i} className="pdbutton">
                        {v}
                    </button>
                ))}
            </div>
            <div className="product-list">
                {reversedData.map((v, i) => (
                    <div key={i} className="product-item chgop">
                        <Link to="/detail" state={{v}}>{/* state={{:}}//**state     */}
                            {/* DetailPg.jsx selData */}
                            <img
                                src={process.env.PUBLIC_URL + v.isrc}
                                alt={v.name}
                                className="product-image"
                            />
                            </Link>
                            <div className="txt-box">
                                {/* :favorite */}
                            <div style={{ cursor: 'pointer' ,float:"right"}}>
                           <IoMdHeart size={20} /> <IoMdHeartEmpty size={20}  />
                            </div>
                                <span>{v.name}</span>
                                <span className="price">
                                    {v.price[0] && <p>{addComma(v.price[0])}</p>}
                                    {v.price[1] && (
                                        <p>
                                            {v.price[1]}&nbsp;{addComma(v.price[2])}
                                        </p>
                                    )}
                                </span>
                            </div>
                        
                    </div>
                ))}
            </div>
        </>
    );
}

export default ProductList;
