//  css ///
import "../../css/top_area.scss";

// 
import $ from "jquery";

/*   */
// import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
// import { faSearch } from "@fortawesome/free-solid-svg-icons";

// //////
import { Link, useNavigate } from "react-router-dom";
/* gnb  */
import { gnbData } from "../data/gnb_data";
import Logo from "../modules/Logo";
import { useContext, useEffect } from "react";
import { dCon } from "../func/dCon";

export default function TopArea() {
    //  
    const myCon = useContext(dCon);
    
    
    //  
    //JSON.parse(localStorage.getItem("mycart-data"))false [];.
    const localsData = JSON.parse(localStorage.getItem("mycart-data")) || [];
   

    /*  :   **************************************************/
    const goNav = useNavigate(); //useNavigate  :goNav

    //1.     
    const enterKey = (e) => {
        //e.keyCode , e.key  .
        // console.log(e.key, e.keyCode);
        if (e.key == "Enter") {
            let txt = $(e.target).val().trim(); //  :val(),trim()   
            console.log("txt", txt);
            //    ( )
            if (txt != "") {
                // txt  

                goSearch(txt);
                $(e.target).val("").blur();
                $("#schin").focus();
            }
        }
    };

    //1.     
    const goSearch = (txt) => {
        console.log("~~~");

        goNav("search", { state: { keyword: txt } });
        $("#schin").focus();

        window.scrollTo(0, 0);
    };

    useEffect(() => {
        /* gnb   useEffect **************************************************/
        let tArea = document.querySelector(".gnb");
        let gnb = document.querySelectorAll(
            ".gnb li:nth-child(n+2):nth-child(-n+5)" //li 2 5  , gnb woman,man,kids,home
        );
        gnb.forEach(
            (ele) =>
                (ele.onmouseover = () => {
                    tArea.classList.add("on");
                })
        );
        tArea.onmouseout = () => {
            tArea.classList.remove("on");
        };
    }, []);

    //   /////////////
    return (
        <>
            {/* 1. */}
            <header className="top-area">
                {/*    */}

                {/*  GNB */}
                <nav className="gnb">
                    <ul>
                        {/* 1.   */}
                        <li>
                            <Link to="/">
                                <Logo logoStyle="top" />
                            </Link>
                        </li>
                        {/* 2. GNB   ///////////////////////////// */}
                        {gnbData.map((v, i) => (
                            <li key={i}>
                                {/* gnb  */}
                                {<Link to={v.link}>{v.txt}</Link>}
                                {
                                    //      /////////////////////
                                    v.sub && (
                                        <div className="smenu">
                                            <aside className="smbx">
                                                <div className="swrap">
                                                    <ol>
                                                        {v.sub.map((v, i) => (
                                                            <li key={i}>
                                                                {/* v.txt    */}
                                                                <Link
                                                                    to={v.link}
                                                                    state={{
                                                                        data: v.data,
                                                                    }}
                                                                    style={{
                                                                        color:
                                                                            v.txt ==
                                                                            " "
                                                                                ? "red"
                                                                                : "",
                                                                    }}
                                                                >
                                                                    {v.txt}
                                                                </Link>
                                                            </li>
                                                        ))}
                                                        <li>
                                                            <Link to={v.link}>
                                                                <img
                                                                    src={
                                                                        process
                                                                            .env
                                                                            .PUBLIC_URL +
                                                                        v.src
                                                                    }
                                                                    alt={v.txt}
                                                                />
                                                                <div className="smenu-right-last-pbox">
                                                                    <p>
                                                                        {
                                                                            v.imagetxt
                                                                        }
                                                                    </p>
                                                                </div>
                                                            </Link>
                                                        </li>
                                                    </ol>
                                                </div>
                                            </aside>
                                        </div>
                                    )
                                }
                            </li>
                        ))}

                        {/* gnb   */}
                        <li>
                            <div className="searchGnb">
                                {/* <FontAwesomeIcon
                                    icon={faSearch}
                                    className="schbtnGnb"
                                    title="Open search"
                                /> */}
                                <div className="gum-box">
                                    <input
                                        type="text"
                                        name="schinGnb" /* name    , id   */
                                        id="schinGnb"
                                        placeholder=""
                                        onKeyUp={enterKey}
                                    />
                                    <span
                                        className="gum"
                                        onClick={(e) => {
                                            let inp =
                                                e.target.previousElementSibling;
                                            if (inp.value.trim() != "") {
                                                goSearch(inp.value);
                                                inp.value = "";
                                            } else {
                                                // alert(" !");
                                                // inp.focus();
                                                goSearch("");
                                            }
                                        }}
                                    >
                                        
                                    </span>
                                </div>
                                <span>
                                    {
                                        /* ,     null  */
                                        myCon.loginSts === null && (
                                            <Link to="/login"></Link>
                                        )
                                    }
                                    {
                                        /*     */
                                        myCon.loginSts !== null && (
                                        
                                               <Link to="/mypage">
                                                   {JSON.parse(myCon.loginSts).unm}

                                               </Link>
                                         
                                        )
                                    }
                                </span>
                                <span>
                                    <Link to="/mycartselpg">({localsData.length})</Link>
                                </span>
                            </div>
                        </li>
                    </ul>
                </nav>
            </header>
        </>
    );
}
