import React, { useContext, useEffect, useState } from "react";
import SwiperDetail from "../plugin/SwiperDetail";
import { useLocation } from "react-router-dom";
import $ from "jquery";
/*  pg scss  */
import "../../css/detail_pg.scss";
import { dCon } from "../func/dCon";
import CareTxt from "../modules/CareTxt";
import { addComma } from "../func/common_fn";

function DetailPg(props) {
  // (Layout.jsxsetCartList    )
  const myCon = useContext(dCon);

  const loc = useLocation();
  const data = loc.state.v; //ProductList.jsx  <Link to="/detail" state={{v}}>   
  console.log("useLocation", loc);
  console.log("ProductList.jsx loc.state  :", data);

  //  :  ,  
  const [toggle, setToggle] = useState(false);
  //   ,      
  const [chgcolor, setChgcolor] = useState(null);

  // -> ////////////////////////////////////////////
  const [showCart, setShowCart] = useState(false); //false 
  useEffect(() => {
    if (showCart) {
      document.querySelector("html").style.overflow = "hidden";
      $(".addedcart-box").animate({ right: "0" });
      $(".cartlist-bg").fadeIn(300);
    } else {
      document.querySelector("html").style.overflow = "auto";
      $(".addedcart-box").animate({ right: "-100%" });
      $(".cartlist-bg").fadeOut(300);
    }
    return () => {
      //~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~
      $(".addedcart-box").stop(true, true); //.addedcart-box  
      $(".cartlist-bg").stop(true, true); //.cartlist-bg  
      document.querySelector("html").style.overflow = "auto"; //html  
    };
  }, [showCart]);

  return (
    <>
    <div className="detailpgmargintop" ></div>
    {/* //  //flex//////////////////////////////////// */}
    <div className="detailbox">
      {/*  //////////////////////////////////// */}
      {/* <div className="care_imgbx"> */}
      <div className="care-box-wrap">
        <div className={`care-box${toggle ? " on" : ""}`}>
          <CareTxt/>

          <button
            className="morebt"
            onClick={() =>{

              //prevState => !prevState    ,toggle 
              setToggle((prevState) => !prevState);
             
            }
            }
            style={{
              border:"none",
              boxSizing:"border-box",
              backgroundColor:"white",
              position: toggle ? "relative" : "absolute",
              bottom: toggle ? "0px" : ".5px", // toggle   top  

             

            }}
            >
            {toggle == true ? "" : " "}
          </button>
        </div>
      </div>
      {/*   ,detail-img */}
      <div className="detail-img">
        <SwiperDetail data={data} />
      </div>
      {/* </div> */}

      {/*   ///flex//////////////////////////////////// */}
      <div className="detail-txtbox">
        <div className="dttxt-bx">
          <div className="tit">{data.name}</div>
          <div className="price">
            {data.price[0] && <span>{addComma(data.price[0])}</span>}
            {data.price[1] && (
              <span>
                {data.price[1]}&nbsp;{addComma(data.price[2])}
              </span>
            )}
          </div>
          <div className="desc">{data.txt}</div>

          <ul className="info">
            <li>    </li>
            <li>,   </li>
          </ul>
        </div>
        <div className="dtsize-bx">
          <div className="color">{data.color}</div>
          <div className="size">
            {data.size.map((v, i) => (
              <button
              key={i}
              className={`${chgcolor === i ? "on" : ""}`}
              onClick={(e) => {
                //:i   
                setChgcolor(i);
                
                //e....nextElementSibling(  ).style:    size    
                e.currentTarget.parentElement.parentElement.nextElementSibling.style.backgroundColor =
                "black";
                e.currentTarget.parentElement.parentElement.nextElementSibling.style.color =
                "#fdfdfd";
                e.currentTarget.parentElement.parentElement.nextElementSibling.style.transition =
                "0.3s 0.2s ease-in";
                
                // .useRef /////////////////
                let dt1 = data.name; // 
                let dt2 = data.isrc; // 
                let dt3 = document.querySelector(".color").innerText; //
                let dt4 = e.target.innerText; // 
                myCon.optVal.current = [dt1, dt2, dt3, dt4]; //.useRef.  dt1,dt2,dt3 
                console.log(
                  "   , ??",
                  myCon.optVal.current[3]
                );
              }}
              >
                {v}
              </button>
            ))}
          </div>
        </div>
        {/* ****************** ****************** */}
        <button
          className="addbutton"
          onClick={() => {
            if (chgcolor === null) {
              alert(" ");
              return;
            }
            
            // :   //////////////////////////////
            //1.  ///////////////
            if (!localStorage.getItem("mycart-data")) {
              //localStorage.getItem("mycart-data") 
              localStorage.setItem("mycart-data", "[]"); // localStorage.setItem(,);. :mycart-data, [ ]
            }
            //2.  /////////???????????????????????????
            let locals = JSON.parse(localStorage.getItem("mycart-data"));
            
            //3.     ///////////////////
            //    idx 
            // gIdx   idx   
            //     !
            
            //******  idx   idx  
            //******   idx    
            //******  
            
            // find 
            let aa = locals.find((v) => {
              if (v.idx == data.idx && v.size == myCon.optVal.current[3])
                return true;
            }); //
            if (aa) {
              //(  .includes())
              alert("  ");
              return;
            }
            
            //4.  ''   (-  )//////////
            locals.push({
              idx: data.idx,
              name: data.name,
              price: data.price[0],
              price1: data.price[1],
              price2: data.price[2],
              color: data.color,
              size: myCon.optVal.current[3],
              isrc: data.isrc,
              cnt:"1",
              /********************** 
               [  ]
               1.: idx
               2.: name
               3.: price
               4.price2:
               5.price3: 
               5.:color
               4.:size
               5.:isrc
               cnt: 
               **********************/
            });
            //5.  (json)  !!!
            //:stringify, :parse
            localStorage.setItem("mycart-data", JSON.stringify(locals));

            //    (:  MyCart.jsx 1       )
            myCon.setLocalsMycart(JSON.stringify(locals));
            
            myCon.setCartList(true); // <CartList/>   //true 
            setShowCart(true);
          }}
          >
          
        </button>
      </div>
      {/*    777 footer-area  none */}
      <style jsx="true">{`
        @media (max-width: 777px) {
          body {
            padding-top: 0;
            }
            .footer-area {
              display: none;
              }
              }
              `}</style>
    </div>
              </>
  );
}

export default DetailPg;
