import React, { useContext } from 'react';
import { dCon } from '../func/dCon';

function MyPage(props) {
    const myCon=useContext(dCon);
    return (
        <div>
            <h1 style={{marginTop:"30vh"}}>  </h1>
            <button onClick={()=>myCon.logoutFn()}>  </button>
        </div>
    );
}

export default MyPage;