import { useEffect, useLayoutEffect, useState,useCallback, useRef } from "react";
import MainArea from "./MainArea";
import TopArea from "./TopArea";
import TopAreaMedia from "./TopAreaMedia";
import { Footer } from "./Footer";
import { useLocation,useNavigate } from "react-router-dom";

//  API  
import { dCon } from "../func/dCon";
import CartList from "../modules/CartList";

//  
export default function Layout() {





    


    // [   ] //////////////////////////////////////////////////////////////
    //  
    const [loginSts, setLoginSts] = useState(sessionStorage.getItem("minfo"));

    //     !
    // const  = useCallback((x)=>{setLoginSts(x)},[loginSts])

    // ->   "minfo" 

    //  :true  
    const [cartList, setCartList] = useState(false);

    //    
    const [loginMsg, setLoginMsg] = useState(null);
    // console.log(loginMsg);

    //   
    const optVal = useRef(null);
    

    //    (-mycart-data)
    const [localsMycart,setLocalsMycart]=useState(localStorage.getItem("mycart-data"));

    console.log(":",localsMycart);
    // [   ] //////////////////////////////////////////////////////////////////
    // 1.   :   useNavigate
    //  useCallback()    !
    const goNav = useNavigate();
    //      !
    //   useCallback() !
    const goPage = useCallback((pm1, pm2) => {
        goNav(pm1, pm2);
    }, []);

    // 2.   
    const makeMsg = useCallback((name) => {
        // 
        let usrIcon = ["", "", "", "", ""];
        //  : 0~4 
        let rdm = Math.floor(Math.random() * 5);
        //    
        setLoginMsg(`Welcome ${name} ${usrIcon[rdm]}`);
    }, []); /////// makeMsg  /////////

    // 3.   /////////
    const logoutFn = useCallback(() => {
        // 1.   null
        setLoginSts(null);
        // 2.   : minfo
        sessionStorage.removeItem("minfo");
        // 3.   
        setLoginMsg(null);
        // 4.   
        goPage("/");
    }, []); //////// logoutFn  /////////
    //  
    //   myCon.logoutFn();

    //    ////////
    useEffect(() => {
        // ->    //////
        //  (minfo)  null 
        //    !
        // -> null   true!
        if (sessionStorage.getItem("minfo")) {
            //  
            let ss = sessionStorage.getItem("minfo");
            //  
            setLoginSts(ss);
            //    :
            // ->  unm() !
            makeMsg(JSON.parse(ss).unm);
        } ///// if ///////
    }, []);


    //   /////////////////////////////////////////////////
    // useEffect(() => {
    //     //   
    //     const handleWheel = (event) => {
    //       //    
    //       console.log('Wheel event detected');
    //     };    
    //     //   
    //     window.addEventListener('wheel', handleWheel, { passive: true });    
    //     //  :      
    //     return () => {
    //       window.removeEventListener('wheel', handleWheel);
    //     };
    //   }, []);
       
       

    // '/' <Footer />  //////////////////////////////////////////////////
    const { pathname } = useLocation();
    console.log("Layout:", pathname);
    let sts = false;

    if (pathname == "/") sts = false;
    else sts = true;
    // '/'  0   70px////////////////////////////////////////////////
    // useEffect(() => {
    //         document.body.style.paddingTop = pathname === "/" ? "0px" : "70px";
    //         return () => {
    //             document.body.style.paddingTop = ""; //    
    //         };
    //     }, [pathname]);
    //1050 <TopAreaMedia />   <TopArea />//////////////////////////////////////////////////
    const [isMobile, setIsMobile] = useState(window.innerWidth <= 1050);
    useEffect(() => {
        const media = () => setIsMobile(window.innerWidth <= 1050);
        window.addEventListener("resize", media);
        return () => window.removeEventListener("resize", media);
    }, []);

    //  ////////////////////////////////////////////////////////
    return (
        // Provider value={{  ,}}
        <dCon.Provider
            value={{
                loginSts,
                setLoginSts,
                loginMsg,
                setLoginMsg,
                goPage,
                makeMsg,
                logoutFn,
                setCartList,
                optVal,
                setLocalsMycart,
               
            }}
        >
            {/*  */}
            {isMobile ? <TopAreaMedia /> : <TopArea />}

            {/*  */}
            <MainArea />
            {/* :cartList true <CartList/>  */}
            {cartList && <CartList optVal={optVal}/>}



            {/* ? */}
            {sts && <Footer />}
            {/* sts true <Footer /> */}
        </dCon.Provider>
    );
} ////////////////////////
